import { CanvaNode } from "./node";
export class CanvaTextNode extends CanvaNode {
    constructor() {
        super(...arguments);
        this.text = "";
    }
    set_pos(y, x) {
        this.y = y;
        this.x = x;
        return this;
    }
    draw(c) {
        c.strokeText(this.text, this.x, this.y);
    }
    set_text(s) {
        this.text = s;
        return this;
    }
}
export function text(s) {
    return new CanvaTextNode().set_text(s);
}
