import web_dom from "../base/web/web_dom";
import { div } from "../base/components/dom/div";
class Constant {
    init() {
        web_dom.init_href();
    }
    get_route(route) {
        return web_dom.url_param["route"] || route;
    }
    init_local() {
    }
    init_body(body) {
        let dialog = div().set_html("x").set_div_style({
            position: "fixed",
            right: 0,
            top: 0
        }).mount(web_dom.get_body());
        web_dom.bind_mousemove(body, (e) => {
            dialog.set_html(e.x + "x" + e.y);
        });
    }
}
export default new Constant();
