export class Node {
    constructor(key) {
        this.code = 0;
        this.type = "";
        this.key = "";
        this.title = "";
        this.value = null;
        this.data = null;
        this.option = null;
        this.parent = null;
        this.childs = null;
        this.direction = -1;
        this.depth = 0;
        this.x = 0;
        this.y = 0;
        this.el = null;
        this.size = 0;
        this.childs = [];
        this.data = {};
        this.key = key;
        this.title = key;
    }
    set_title(title = "") {
        this.title = title;
        return this;
    }
    set_data(data) {
        for (var key in data) {
            this.data[key] = data[key];
        }
        return this;
    }
    set_value(value) {
        this.value = value;
        return this;
    }
    set_childs(childs) {
        this.childs = [];
        for (var i = 0; i < childs.length; i++) {
            if (childs[i] instanceof Node) {
                this.add_child(childs[i]);
            }
            else {
                this.add_child(new Node().set_option(childs[i]));
            }
        }
        return this;
    }
    set_size(size) {
        this.size = size;
        return this;
    }
    set_direction(direction) {
        this.direction = direction;
        return this;
    }
    set_option(data) {
        if (data.key) {
            this.key = data.key;
        }
        if (data.data) {
            this.set_data(data.data);
        }
        return this.set_title(data.title).set_value(data.value).set_childs(data.childs || []).set_type(data.type).set_size(data.size).set_direction(data.direction);
    }
    dump() {
        return {
            type: this.type,
            value: this.value,
            title: this.title,
            data: this.data
        };
    }
    add_child(v) {
        this.childs.push(v);
        v.parent = this;
    }
    filter(key) {
        let ret = new Node().set_childs(this.childs.filter(v => {
            return v.title.indexOf(key) != -1;
        }));
        return ret;
    }
    init_layout() {
        let ret = { y: 0, x: 0 };
        this.x = 0;
        function dfs(node, p) {
            if (node.childs.length == 0) {
                node.x = ret.x;
                ret.x += 1;
                return node.x;
            }
            for (var i = 0; i < node.childs.length; i++) {
                node.childs[i].y = node.y + 1;
                ret.y = Math.max(ret.y, node.childs[i].y);
                node.x += dfs(node.childs[i], node);
            }
            node.x = node.x / node.childs.length;
            return node.x;
        }
        dfs(this, null);
        ret.x -= 1;
        console.warn(ret.x, ret.y);
        return ret;
    }
    set_type(type) {
        this.type = type;
        return this;
    }
    get_title() {
        return this.title;
    }
}
export function to_node(n) {
    if (n instanceof Node) {
        return n;
    }
    return new Node().set_option(n);
}
export function node(key) {
    return new Node(key);
}
export function not_null(a, b) {
    if (a == "" || a == null || a == undefined) {
        return b;
    }
    return a;
}
