import { Node } from "./cls";
import web_dom from "./web_dom";
import util from "../tool/util";
import MOCK_DATA from "../../model/mock";
function fack_data(depth, max_dp, len) {
    let ret = new Node().set_title("root");
    if (depth == max_dp) {
        return ret;
    }
    let childs = [];
    for (var i = 0; i < len; i++) {
        childs.push(fack_data(depth + 1, max_dp, len).set_title(`title_${depth}_${i}`).set_value(`value_${depth}_${i}`));
    }
    ret.set_childs(childs);
    return ret;
}
class Constant {
    constructor() {
        this.DEFAULT_MARGIN = 4;
        this.DEFAULT_PADDING = 3;
        this.DEFAULT_LINE_HEIGHT = 40;
        this.MOCK_KEY = "MOCK_KEY";
        this.HORIZONTAL = 0;
        this.VERTICAL = 1;
        this.COLOR_WHITE = '#fff';
        this.COLOR_YELLOW = '#ff0';
        this.COLOR_BALCK = '#000';
        this.COLOR_GRAY = '#888';
        this.DIALOG_COLOR = '#8888';
        this.INPUT_NUMBER_WIDTH = 40;
        this.INPUT_STRING_MIN_WIDTH = 120;
        this.TEXT_AREA_WIDTH = 600;
        this.TEXT_AREA_HEIGHT_1 = 100;
        this.TEXT_AREA_HEIGHT_2 = 200;
        this.TEXT_AREA_HEIGHT_3 = 400;
        this.KEY_RIGHT = 'ArrowRight';
        this.KEY_LEFT = 'ArrowLeft';
        this.MOCK_NODE_3_20 = fack_data(0, 3, 20);
        this.MOCK_NODE_3_5 = fack_data(0, 3, 5);
        this.MOCK_NODE_3_3 = fack_data(0, 3, 3);
    }
    get_mock_data(s, param) {
        if (web_dom.web_host.endsWith('github.io')) {
            return this.get_ts_data(s, param);
        }
        return null;
    }
    get_ts_data(s, param) {
        let param_hash = util.hash_any(param);
        if (MOCK_DATA[s] && MOCK_DATA[s][param_hash])
            return MOCK_DATA[s][param_hash];
        return null;
    }
}
export default new Constant();
